<template>
  <svg id="main-slide" ref="mainSlide" preserveAspectRatio :width="svgWidth" :height="svgHeight" :viewBox="'0 0 ' + slideSizeArray.join(' ')">
    <text x="25" y="50" font-size="32">{{sildeTitle}}</text>
    <circle-pack-chart x="25" y="40" :width="slideSizeArray[0]" :height="slideSizeArray[1] - 100"></circle-pack-chart>
    <text x="25" :y="slideSizeArray[1] - 25" font-size="16">{{slideSource}}</text>
  </svg>
</template>

<script>
import {saveSvgAsPng} from 'save-svg-as-png'
import saveSVG from '@/utils/saveSVG'
import CirclePackChart from './CirclePackChart.vue'
import { mapState, mapGetters } from 'vuex'

export default {
  name: 'SlideContainer',
  data() {
    return {
      svgHeight: 0
    }
  },
  components: {
    CirclePackChart
  },
  mounted (){
    this.svgHeight = this.$el.parentNode.clientHeight
  },
  computed: {
    ...mapState(['sildeTitle', 'slideSource']),
    ...mapGetters(['slideSizeArray']),
    aspectRatio () {
      return this.slideSizeArray[0] / this.slideSizeArray[1]
    },
    svgWidth () {
      return this.svgHeight * this.aspectRatio
    },
  },
  methods: {
    exportImage (format) {
      switch(format) {
        case 'png':
          saveSvgAsPng(this.$refs.mainSlide, "chart.png")
        case 'svg':
          saveSVG(this.$refs.mainSlide)
      }
    }
  },
  watch: {
    '$el.parentNode.clientHeight' () {
      this.svgHeight = this.$el.parentNode.clientHeight
    }
  }
}
</script>

<style lang="stylus" scoped>
#main-slide
  background-color #ffffff
</style>